import React from "react"
import styled from "styled-components"
import { MyIcon } from "@macashipo/mlib"
import { mergeRecursive } from "src/helpers/Flows/MNeedUpdateMLib"
import { Bar, Line } from "react-chartjs-2"
const Wrapped = styled.div`
  box-shadow: 0 0 1px rgb(0 0 0 / 13%), 0 1px 3px rgb(0 0 0 / 20%);
  border-radius: 0.25rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1rem;
  min-height: 80px;
  padding: 0.5rem;
  position: relative;
  width: 100%;
  flex-direction: column;
  & .a-top {
    & .a-value {
      font-size: 2rem;
      font-weight: 700;
      padding: 0;
      white-space: nowrap;
      line-height: 2rem;
    }
  }
`
export default function WithMyUI(props) {
  const data = props.data || {}
  // console.warn("data", data)
  let _title = data.title || ""
  let _value = data.value || ""
  let _color = data.color || "white"
  let _bg = data.bg || "green"

  let _chartData = data.chartData || {}
  let _chartOptions = data.chartOptions || {}
  // const cardChartData4 = {
  //   labels: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
  //   datasets: [
  //     {
  //       label: "Error",
  //       backgroundColor: "rgba(255,255,255,.3)",
  //       borderColor: "transparent",
  //       data: [78, 81, 80, 45, 34, 12, 40, 75, 34, 89, 32, 68, 54, 72, 18, 98],
  //     },
  //   ],
  // }

  if (_chartData && _chartData.datasets && _chartData.datasets.length > 0) {
    const chartOptionsDefault = {
      maintainAspectRatio: false,
      plugins: {
        legend: {
          display: false,
        },
      },
      scales: {
        x: {
          display: false,
          barPercentage: 0.6,
        },

        y: {
          display: false,
        },
      },
    }
    _chartOptions = mergeRecursive(chartOptionsDefault, _chartOptions)
    return (
      <Wrapped style={{ color: _color, background: _bg }}>
        <div className="a-top">
          <div className="a-value">{_value}</div>
          <div className="a-title">{_title}</div>
        </div>
        <div className="a-chart">
          <Bar data={_chartData} options={_chartOptions} height={70} />
        </div>
      </Wrapped>
    )
  }
  console.warn("render widget bar, missing chartData")
  return <div></div>
}
