import React from "react"
import styled from "styled-components"
import { MyIcon } from "@macashipo/mlib"
import { Bar, Pie } from "react-chartjs-2"
const Wrapped = styled.div`
  box-shadow: 0 0 1px rgb(0 0 0 / 13%), 0 1px 3px rgb(0 0 0 / 20%);
  border-radius: 0.25rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1rem;
  min-height: 80px;
  padding: 0.5rem;
  position: relative;
  width: 100%;
  flex-direction: column;
  & .a-top {
    & .a-value {
      font-size: 2rem;
      font-weight: 700;
      padding: 0;
      white-space: nowrap;
      line-height: 2rem;
    }
  }
`
export default function WithMyUI(props) {
  const data = props.data || {}
  // console.warn("data", data)
  let _title = data.title || ""
  let _value = data.value || ""
  let _color = data.color || "white"
  let _bg = data.bg || "green"
  let _chartData = data.chartData || {}
  let _chartOptions = data.chartOptions || {}
  // const cardChartData4 = {
  //   labels: ["Red", "Blue", "Yellow"],
  //   datasets: [
  //     {
  //       label: "My First Dataset",
  //       data: [300, 50, 100],
  //       backgroundColor: [
  //         "rgb(255, 99, 132)",
  //         "rgb(54, 162, 235)",
  //         "rgb(255, 205, 86)",
  //       ],
  //       hoverOffset: 4,
  //     },
  //   ],
  // }

  // const cardChartOpts4 = {
  //   maintainAspectRatio: false,
  //   plugins: {
  //     legend: {
  //       display: false,
  //     },
  //   },
  // }
  return (
    <Wrapped style={{ color: _color, background: _bg }}>
      <div className="a-top">
        <div className="a-value">{_value}</div>
        <div className="a-title">{_title}</div>
      </div>
      <div className="a-chart">
        <Pie data={_chartData} options={_chartOptions} height={70} />
      </div>
    </Wrapped>
  )
}
