import React from "react"
import styled from "styled-components"
import { Progress } from "reactstrap"
import { MyIcon } from "@macashipo/mlib"
const Wrapped = styled.div`
  box-shadow: 0 0 1px rgb(0 0 0 / 13%), 0 1px 3px rgb(0 0 0 / 20%);
  border-radius: 0.25rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  min-height: 80px;
  position: relative;
  width: 100%;
  & .a-main {
    padding: 0.5rem;
    & .a-value {
      font-size: 2.2rem;
      font-weight: 700;
      margin: 0 0 10px;
      padding: 0;
      white-space: nowrap;
    }
    & .a-title {
      margin-bottom: 1rem;
    }
  }
  & .a-icon {
    display: flex;
    font-size: 90px;
    position: absolute;
    right: 15px;
    top: 15px;
  }
  & .a-more {
    background-color: rgba(0, 0, 0, 0.1);
    display: block;
    padding: 3px 0;
    position: relative;
    text-align: center;
    text-decoration: none;
    z-index: 10;
    min-height: 30px;
    &:hover {
      background-color: rgba(0, 0, 0, 0.15);
    }
  }
`
export default function WithMyUI(props) {
  const data = props.data || {}
  console.warn("data", data)
  let _icon = data.icon || "fa fa-star"
  let _title = data.title || ""
  let _value = data.value || ""
  let _color = data.color || "white"
  let _colorIcon = data.colorIcon || "rgba(0,0,0,.15)"
  let _bg = data.bg || "green"
  let _link = data.link
  let _linkTarget = data.linkTarget || "_blank"
  let _linkTitle = data.linkTitle || "More info"
  return (
    <Wrapped style={{ color: _color, background: _bg }}>
      <div className="a-main">
        <div className="a-value">{_value}</div>
        <div className="a-title">{_title}</div>
      </div>
      <div className="a-icon">
        <MyIcon data={_icon} style={{ color: _colorIcon }} />
      </div>
      <div className="a-more">
        {_link && (
          <a href={_link} target={_linkTarget} style={{ color: _color }}>
            {_linkTitle}
            <i className="fa fa-arrow-circle-right" />
          </a>
        )}
      </div>
    </Wrapped>
  )
}
