import { MyPage } from "@macashipo/mlib"
import { FlowBuildColClassName } from "src/helpers/Flows/ConfigPage"
export default function WithMyUI(props) {
  const fnList = props.fnList || {}
  const data = props.data || {}
  const configPage = props.configPage || {}
  const { title } = data
  const { Contents } = configPage
  // console.warn("data", data, props)
  return (
    <div>
      {title && <div className="">{title}</div>}
      <div className="row">
        {Contents &&
          Contents.length > 0 &&
          Contents.map((v, i) => {
            return (
              <div key={i} className={FlowBuildColClassName(v)}>
                <MyPage configPage={v} fnList={fnList} />
              </div>
            )
          })}
      </div>
    </div>
  )
}
