import { HJson, MyUI, MyPage } from "@macashipo/mlib"
import React from "react"
import useConfigPage from "../../hooks/useConfigPage"
import { FlowBuildColClassName } from "src/helpers/Flows/ConfigPage"
import WidgetHelpers from "./DashboardWidgets/initWidget"

//init widget
WidgetHelpers.init()

export default function AdminDashboard(props) {
  const cfp = useConfigPage(props.configPage, { needContents: true, props })
  console.warn("admintab:", props)
  if (cfp.showLoading) {
    return cfp.renderLoading()
  }
  if (cfp.errorMsg) {
    return cfp.renderError(cfp.errorMsg)
  }
  return (
    <div className="mp-admin_dashboard row">
      {cfp.configPageExt &&
        cfp.configPageExt.Contents &&
        cfp.configPageExt.Contents.map((item, index) => {
          return (
            <div key={index} className={FlowBuildColClassName(item)}>
              <MyPage configPage={item} fnList={cfp.fnList} />
            </div>
          )
        })}
    </div>
  )
}
