import React from "react"
import styled from "styled-components"
import { MyIcon, HUtils } from "@macashipo/mlib"
import { Bar, Line } from "react-chartjs-2"
import { mergeRecursive } from "src/helpers/Flows/MNeedUpdateMLib"
const Wrapped = styled.div`
  box-shadow: 0 0 1px rgb(0 0 0 / 13%), 0 1px 3px rgb(0 0 0 / 20%);
  border-radius: 0.25rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1rem;
  min-height: 80px;
  padding: 0.5rem;
  position: relative;
  width: 100%;
  flex-direction: column;
  & .a-top {
    & .a-value {
      font-size: 2rem;
      font-weight: 700;
      padding: 0;
      white-space: nowrap;
      line-height: 2rem;
    }
  }
`
export default function WithMyUI(props) {
  const data = props.data || {}
  // console.warn("data line", data)
  let _title = data.title || ""
  let _value = data.value || ""
  let _color = data.color || "white"
  let _bg = data.bg || "green"
  let _chartData = data.chartData || {}
  let _chartOptions = data.chartOptions || {}
  // const cardChartData1 = {
  //   labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul"],
  //   datasets: [
  //     {
  //       label: "Data",
  //       backgroundColor: "#fff",
  //       borderColor: "rgba(255,255,255,.55)",
  //       data: [65, 59, 84, 84, 51, 55, 80],
  //     },
  //   ],
  // }

  if (_chartData && _chartData.datasets && _chartData.datasets.length > 0) {
    const chartOptionsDefault = {
      maintainAspectRatio: false,
      plugins: {
        legend: {
          display: false,
        },
      },
      scales: {
        x: {
          display: false,
        },
        y: {
          display: false,
          ticks: {
            display: false,
            min: Math.min.apply(Math, _chartData.datasets[0].data) - 5,
            max: Math.max.apply(Math, _chartData.datasets[0].data) + 5,
          },
        },
      },
      elements: {
        line: {
          borderWidth: 1,
        },
        point: {
          radius: 4,
          hitRadius: 10,
          hoverRadius: 4,
        },
      },
    }

    _chartOptions = mergeRecursive(chartOptionsDefault, _chartOptions)
    // console.warn("_chartOptions:", _chartOptions)
  }
  // console.warn("render lie:", _chartData, _chartOptions)
  return (
    <Wrapped style={{ color: _color, background: _bg }}>
      <div className="a-top">
        <div className="a-value">{_value}</div>
        <div className="a-title">{_title}</div>
      </div>
      <div className="a-chart">
        <Line data={_chartData} options={_chartOptions} height={70} />
      </div>
    </Wrapped>
  )
}
