import { HJson, MyUI } from "@macashipo/mlib"
import { config } from "process"

export const FlowGetConfigOfConfigPage = (configPage = {}) => {
  let _configOfConfigPage = {}
  if (configPage && configPage._ConfigObj) {
    _configOfConfigPage = configPage._ConfigObj
  } else if (configPage && configPage.Config) {
    if (typeof configPage.Config == "string") {
      configPage._ConfigObj = HJson.getObj(configPage.Config)
      _configOfConfigPage = configPage._ConfigObj
    } else {
      configPage._ConfigObj = configPage.Config
      _configOfConfigPage = configPage._ConfigObj
    }
  }
  return _configOfConfigPage
}

export const FlowBuildColClassName = (configPage = {}) => {
  let _configOfConfigPage = FlowGetConfigOfConfigPage(configPage)
  let _className = ""
  let _colConfig = {
    default: 12,
    ..._configOfConfigPage.Col,
  }
  if (_configOfConfigPage.colClassName) {
    return _configOfConfigPage.colClassName
  }
  let _keys = Object.keys(_colConfig)
  if (_keys && _keys.length > 0) {
    for (let k of _keys) {
      let _thisClassname = `col-${k}-${_colConfig[k]}`
      if (k == "default") {
        _thisClassname = `col-${_colConfig[k]}`
      }
      _className += " " + _thisClassname
    }
  }
  return _className
}

export const FlowGetConfigMyUI = (configPage = {}) => {
  let _configOfConfigPage = FlowGetConfigOfConfigPage(configPage)
  if (_configOfConfigPage.myui) {
    return _configOfConfigPage.myui
  } else if (_configOfConfigPage.type) {
    return {
      type: _configOfConfigPage.type,
      ..._configOfConfigPage.props,
    }
  }
  console.warn("no config myui", _configOfConfigPage)
  return {}
}
