import React from "react"
import styled from "styled-components"
import { Progress } from "reactstrap"
import { MyIcon } from "@macashipo/mlib"
const Wrapped = styled.div`
  box-shadow: 0 0 1px rgb(0 0 0 / 13%), 0 1px 3px rgb(0 0 0 / 20%);
  border-radius: 0.25rem;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1rem;
  min-height: 80px;
  padding: 0.5rem;
  position: relative;
  width: 100%;
  & .a-left {
    border-radius: 0.25rem;
    -ms-flex-align: center;
    align-items: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.875rem;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
    width: 70px;
  }
  & .a-right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    line-height: 1.8;
    -ms-flex: 1;
    flex: 1;
    padding: 0 10px;
    overflow: hidden;
    & .a-title {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    & .a-value {
      display: block;
      margin-top: 0.25rem;
      font-weight: 700;
    }
  }
`
export default function WithMyUI(props) {
  const data = props.data || {}
  console.warn("data", data)
  let _icon = data.icon || "fa fa-star"
  let _title = data.title || ""
  let _value = data.value || ""
  let _des = data.des || ""
  let _color = data.color || "white"
  let _bg = data.bg || "green"
  let _valuePercent = data.valuePercent || 50
  return (
    <Wrapped style={{ color: _color, background: _bg }}>
      <div className="a-left">
        <MyIcon data={_icon} />
      </div>
      <div className="a-right">
        <div className="a-title">{_title}</div>
        <div className="a-value">{_value}</div>
        <div className="a-progress">
          <Progress
            value={_valuePercent}
            color="light"
            style={{ height: "2px", backgroundColor: "#0000002b" }}
          />
        </div>
        <div className="a-des">{_des}</div>
      </div>
    </Wrapped>
  )
}
